* {
  border-color: var(--border);
}
body {
  background-color: var(--background);
  color: var(--foreground);
  font-family: var(--font-sans);
}
html {
  scroll-behavior: smooth;
}
*:focus-visible {
  outline: 2px solid var(--ring);
  outline-offset: 2px;
}
.dark ::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
.dark ::-webkit-scrollbar-track {
  background: var(--muted);
  border-radius: 4px;
}
.dark ::-webkit-scrollbar-thumb {
  background: var(--border);
  border-radius: 4px;
}
.dark ::-webkit-scrollbar-thumb:hover {
  background: var(--input);
}
.logo-shadow {
  box-shadow: 0 8px 22px rgba(0, 0, 0, 0.28);
}
.dark .logo-shadow {
  box-shadow: 0 8px 22px rgba(255, 255, 255, 0.18);
}
.glass {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}
.dark .glass {
  background: rgba(30, 30, 30, 0.7);
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.glow-card {
  transition: box-shadow 0.3s ease, transform 0.2s ease;
}
.glow-card:hover {
  box-shadow: 0 0 30px rgba(32, 178, 170, 0.15), 0 4px 20px rgba(0, 0, 0, 0.08);
  transform: translateY(-2px);
}
.dark .glow-card:hover {
  box-shadow: 0 0 30px rgba(76, 175, 80, 0.2), 0 4px 20px rgba(0, 0, 0, 0.3);
}
.glow-primary {
  transition: box-shadow 0.3s ease;
}
.glow-primary:hover {
  box-shadow: 0 0 20px rgba(32, 178, 170, 0.4);
}
.dark .glow-primary:hover {
  box-shadow: 0 0 20px rgba(76, 175, 80, 0.4);
}
.blur-bg {
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}
@layer base {
  * {
    border-color: var(--border);
    outline-color: var(--ring);
    @supports (color: color-mix(in lab, red, red)) {
      outline-color: color-mix(in oklab, var(--ring) 50%, transparent);
    }
  }
  body {
    background-color: var(--background);
    color: var(--foreground);
  }
}
